import React from 'react';
import { Radar } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    RadialLinearScale,
    PointElement,
    LineElement,
    Filler,
    Tooltip,
    Legend,
} from 'chart.js';
import ss from "./RadarChart.module.scss"

// Chart.js  
ChartJS.register(
    RadialLinearScale,
    PointElement,
    LineElement,
    Filler,
    Tooltip,
    Legend
);

const MAX_BALL_TOUCHES = 30;
const MAX_KEY_PASSES = 5;
const MAX_ATTACK_POINTS = 5;
const MAX_ENGAGEMENTS = 10;

const RadarChart = ({ playerState, playerName = "Player 1" }) => {

    const calculateDerivedValues = (state) => {
        const passSuccess = state["forwardPass"] + state["sidePass"] + state["backPass"];
        const passTries = passSuccess + state["failedPass"];
        const passSuccessRate = (passSuccess * 100 / passTries).toFixed(2);
        const ballTouches = passTries + state["shot"] + state["dribble"] + state["failedDribble"] + state["successfulDuel"];

        return {
            "": ((state["shot"] + state["assist"]) / MAX_ATTACK_POINTS).toFixed(2) * 100,
            " ": passSuccessRate,
            "": (state["keyPass"] / MAX_KEY_PASSES).toFixed(2) * 100,
            " ": (ballTouches / MAX_BALL_TOUCHES).toFixed(2) * 100,
            " ": ((state["intercept"] + state["successfulDuel"] + state["failedDuel"]) / MAX_ENGAGEMENTS).toFixed(2) * 100,
            " ": ((ballTouches - state["turnover"]) / ballTouches).toFixed(2) * 100,
        };
    };


    // Radar   labels values 
    const generateLabelsAndValues = (state) => {
        const derivedValues = calculateDerivedValues(state);
        const labels = Object.keys(derivedValues); //  label 
        const values = Object.values(derivedValues); //  value 

        return { labels, values };
    };

    const { labels, values } = generateLabelsAndValues(playerState);

    // Radar chart 
    const data = {
        labels: labels, //   labels 
        datasets: [
            {
                label: playerName,
                data: values, //   values 
                backgroundColor: 'rgba(34, 202, 236, 0.2)',
                borderColor: 'rgba(34, 202, 236, 1)',
                borderWidth: 2,
            },
        ],
    };

    const options = {
        scales: {
            r: {
                suggestedMin: 0,
                suggestedMax: 100, //    
                ticks: {
                    beginAtZero: true,
                },
            },
        },
    };

    if (!data) return null

    return (
        <div className={ss.bg}>
            <Radar data={data} options={options} />
        </div>
    );
};

export default RadarChart;
